.App{display:grid;grid-template-columns:1fr 2fr 1fr 1fr;grid-template-rows:auto;grid-template-areas:"header header header header" "main main main sidebar" "footer footer footer footer";width:100%;height:100%}a,input{outline:none;text-decoration:none;color:#141414}.card{max-width:155px;max-height:350px;text-align:center;display:flex;justify-content:center;align-items:flex-start;margin:10.5px 10.5px 0 10.5px}.card .imgWrapper{max-width:100%;max-height:100%;display:flex;justify-content:center;align-items:center;overflow:hidden;border-radius:10px;box-shadow:5px 5px 10px grey}.card .imgWrapper:hover{animation:100ms ease-in-out big;animation-fill-mode:both;animation-direction:alternate}.card .imgWrapper img{width:100%;height:90%}.card h3{padding-top:1.4%}@media screen and (max-width: 550px){.card{width:calc(100% / 3);height:calc(100% / 3)}}@media screen and (max-width: 550px){.App{display:grid;grid-template-columns:1fr;grid-template-rows:auto auto 1px;grid-template-areas:"header header header" "main main sidebar" "footer footer footer";width:100%;height:100%}}@keyframes big{to{transform:scale(1.2)}}.pageListWrapper{width:100%;height:auto;margin:2% 0 2% 0;display:flex;flex-direction:row;align-items:center;justify-content:space-around}.pageListButton{padding:1%;font-size:1.2em}.copy-wrapper{position:absolute;max-width:50%;top:30%;left:5%}.sidebar-wrapper{background-color:#2d323c;border-left:1px solid black;grid-area:sidebar}.sidebar-wrapper .genre-card,.sidebar-wrapper .sidebar-header{display:flex;justify-content:center;align-items:center;border:1px solid white;color:white}.sidebar-wrapper .genre-card h3,.sidebar-wrapper .sidebar-header h3{letter-spacing:2px}.sidebar-wrapper .sidebar-header{background-color:#ac5f12}.sidebar-wrapper .genre-card h3{letter-spacing:2px}.sidebar-wrapper .genre-card:hover{animation:asideHover 0.1s cubic-bezier(0.6, -0.28, 0.735, 0.045);animation-fill-mode:both;animation-direction:alternate}@media screen and (max-width: 550px){.sidebar-wrapper