apper .search .searchBar{width:50%;height:70%;border-radius:5%;font-size:1.6em}.header-wrapper .search .searchButton{height:70%;width:15%;display:flex;justify-content:center;align-items:center;font-size:1.4em;margin-left:2%;padding:0.1em;color:white;background:linear-gradient(90deg, #f30, #3f19c7);border-radius:5%}.header-wrapper .search .searchButton:hover{animation:gradiSearch 0.1s linear;animation-fill-mode:both}.header-wrapper .recommend{height:70%;width:20%;display:flex;justify-content:center;align-items:center;border-radius:5%;margin-left:2%;background:linear-gradient(90deg, #3f19c7, #f30);padding:0.1em}.header-wrapper .recommend .recommendButton{color:white;font-size:1.4em}.header-wrapper .recommend:hover{animation:gradiRec 0.1s linear;animation-fill-mode:both}@media screen and (max-width: 900px){.header-wrapper .recommend{display:none}}@keyframes gradiRec{from{background:linear-gradient(90deg, #3f19c7, #f30)}40%{background:linear-gradient(150deg, #3f19c7, #f30)}80%{background:linear-gradient(210deg, #3f19c7, #f30)}to{background:linear-gradient(270deg, #3f19c7, #f30)}}@keyframes gradiSearch{from{background:linear-gradient(90deg, #f30, #3f19c7)}40%{background:linear-gradient(150deg, #f30, #3f19c7)}80%{background:linear-gradient(210deg, #f30, #3f19c7)}to{background:linear-gradient(270deg, #f30, #3f19c7)}}
